import React, { useEffect, useMemo, useState } from 'react';

//     Material React Table
import MaterialReactTable, {
  type MRT_ColumnDef,
  type MRT_PaginationState,
} from 'material-react-table';

import { MRT_Localization_RU } from 'material-react-table/locales/ru';

import Button from '@mui/material/Button';

//     Notistack
import { SnackbarProvider, enqueueSnackbar, closeSnackbar } from 'notistack';

//    API
type apiResponse = {
  data: Array<User>;
  meta: {
    totalRowCount: number;
  };
};

//   
type User = {
  avatar: string;
  first_name: string;
  last_name: string;
  email: string;
  id: string;
};

const Table = () => {
  //    
  const [data, setData] = useState<User[]>([]);
  const [isError, setIsError] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [isRefetching, setIsRefetching] = useState(false);
  const [rowCount, setRowCount] = useState(12);

  //  
  const [pagination, setPagination] = useState<MRT_PaginationState>({
    pageIndex: 0,
    pageSize: 6,
  });

  useEffect(() => {
    const fetchData = async () => {
      if (!data.length) {
        setIsLoading(true);
      } else {
        setIsRefetching(true);
      }

      //    
      const url = new URL(
        '/api/users',
        'https://reqres.in/'
      );
      url.searchParams.set(
        'page',
        `${pagination.pageIndex + 1}`,
      );
      url.searchParams.set('per_page', `${pagination.pageSize}`);

      try {
        const startTime = new Date().getTime();
        const response = await fetch(url.href);
        //  ,    
        const fetchTime = (new Date().getTime() - startTime)/1000;
        //        
        response.status < 300 ? enqueueSnackbar(`    ${fetchTime} `, {variant: "success"}) :
          (response.status >= 400 && response.status < 500) ? enqueueSnackbar('   ', {variant: "error"}) :
          response.status >= 500 ? enqueueSnackbar('   ', {variant: "error"}) : 
          enqueueSnackbar('  ', {variant: "error"});
        const json = (await response.json()) as apiResponse;
        //     state
        setData(json.data);
      } catch (error) {
        setIsError(true);
        console.error(error);
        return;
      }
      setIsError(false);
      setIsLoading(false);
      setIsRefetching(false);
    };
    fetchData();
  }, [
    pagination.pageIndex,
    pagination.pageSize,
  ]);


  const columns = useMemo<MRT_ColumnDef<User>[]>(
    () => [
      //     
      {
        accessorKey: 'avatar',
        header: '',
        Cell: ({ cell }) => (
          <img alt=' ' src={cell.getValue<string>()}></img>
        ),
      },
      {
        accessorKey: 'first_name',
        header: '',
      },
      {
        accessorKey: 'last_name',
        header: '',
      },
      {
        accessorKey: 'email',
        header: '',
      },
      {
        accessorKey: 'id',
        header: 'ID',
      },
    ],
    [],
  );

  return (
    //    
    <SnackbarProvider maxSnack={5} autoHideDuration={5000} anchorOrigin = {{ vertical: "top", horizontal: "right" }} action={(snackbarId) => (
      //     
      <Button
          size='small'
          sx={{
            color: 'white'
          }}
          onClick={() => closeSnackbar(snackbarId)}
      >
          
      </Button>
    )}>
      <MaterialReactTable
        columns={columns}
        data={data}
        localization={MRT_Localization_RU}
        //      -    
        enableRowSelection={false}
        enableColumnResizing={false}
        enableColumnActions={false}
        enableColumnFilters={false}
        enablePagination={true}
        muiTablePaginationProps={{
          rowsPerPageOptions: [],
        }}
        enableSorting={false}
        enableBottomToolbar={true}
        enableTopToolbar={isError? true : false}
        enableToolbarInternalActions={false}
        enableStickyHeader={true}
        muiTableHeadRowProps={{
          sx: {
            height: '50px'
          }
        }}
        muiTableHeadCellProps={{
          sx: {
            fontSize: '35px'
          }
        }}
        muiTableBodyCellProps={{
          sx: {
            fontSize: '18px'
          }
        }}
        getRowId={(row) => row.id}
        manualPagination
        muiToolbarAlertBannerProps={
          isError
            ? {
                color: 'error',
                children: '   ',
              }
            : undefined
        }
        onPaginationChange={setPagination}
        rowCount={rowCount}
        state={{
          isLoading,
          pagination,
          showAlertBanner: isError,
          showProgressBars: isRefetching,
        }}
      />
    </SnackbarProvider>
  );
};

export default Table;
